@use 'variables' as *;
@use 'mixins.scss' as *;
@use 'extend.scss' as *;

html {
    font: {
        style: normal;
        size: 62.5%;
        weight: $font-weight-default;
        family: $font-family-default;
    }
    height: 100%;
    counter-reset: heading;
    letter-spacing: 1.1px;
    color: var(--font-color-default);

    @include screen-1024 {
        font-size: 50%;
    }
}

body {
    overscroll-behavior: none;
    font: 3rem/3 $font-family-default;
    background: $background;

    &.no-scroll {
        overflow: hidden;
    }

    @include screen-490 {
        font: 1.5rem/1.5 $font-family-default;
    }
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    border: none;
    -webkit-tap-highlight-color: transparent;
    // scrollbar-width: thin;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

img,
svg {
    vertical-align: top;
    max-width: 100%;
    height: auto;
    pointer-events: none;
}

a {
    cursor: pointer;
    text-decoration: none;
    color: inherit;
}

a:hover {
    text-decoration: none;
    vertical-align: baseline;
    background-color: transparent;
}

a:focus {
    text-decoration: none;
}

a:visited {
    vertical-align: middle;
    text-emphasis-style: none;
}

h2,
h3 {
    font-family: $font-family-title;
}

input,
button,
textarea {
    border: none;
}

button,
input[type='submit'] {
    display: inline-block;
    cursor: pointer;
    background: transparent none;
    box-shadow: none;
}

// input::-ms-clear {
// 	display: none;
// }

input,
textarea,
button,
select {
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    background-color: transparent;
}

input:focus,
input:active,
button:focus,
button:active {
    outline: none;
}

input,
textarea,
button,
select {
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    background-color: transparent;
}

button::-moz-focus-inner {
    padding: 0;
    border: 0;
}

label {
    cursor: pointer;
}

a:focus,
a:active,
input:focus,
textarea:focus {
    outline: none;
}

ul,
ol,
li {
    list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font: {
        weight: inherit;
        style: inherit;
    }
}

//  * Изменить стиль выделения текста
::selection {
    color: $text-color-selection;
    background-color: $text-selection;
    text-shadow: 0 1px 0 #000;
}

::-moz-selection {
    color: $text-color-selection;
    background-color: $text-selection;
    text-shadow: 0 1px 0 #000;
}

/// * Изменить стиль полосы прокрутки (scrollbar)
::-webkit-scrollbar {
    width: 0.8rem;
    padding: 0.031em;
}

::-webkit-scrollbar-track {
    background-color: var(--scrollbar-thumb-color);
}

::-webkit-scrollbar-thumb {
    border: 1px solid var(--border-color);
    border-radius: 2px;
    background-color: var(--thumb-color);
}

::-webkit-scrollbar-thumb:hover {
    background-color: var(--background-hover);
}
