.preloader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #000;
}
.preloader__loader {
  width: 48px;
  height: 48px;
  border: 3px solid #fff;
  border-radius: 50%;
  display: inline-block;
  position: relative;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
}
.preloader__loader::after {
  content: "";
  box-sizing: border-box;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: 3px solid;
  border-color: #ff3d00 transparent;
}

.preloader-remove {
  display: none;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.burger-button {
  cursor: pointer;
  transition: opacity 0.5s ease-in;
}
.burger-button__line {
  width: 20px;
  height: 2px;
  background-color: #fff;
  display: block;
  margin: 6px auto;
  transition: all 0.3s ease-in-out;
}
.burger-button.is-active .burger-button__line {
  background-color: #fff;
}
.burger-button.is-active .burger-button__line:nth-child(1) {
  transform: translateY(8px);
}
.burger-button.is-active .burger-button__line:nth-child(3) {
  transform: translateY(-8px);
}

.sidebar-menu {
  pointer-events: all;
  position: fixed;
  width: 100%;
  height: calc(100vh - 80px);
  right: 0;
  top: 80px;
  z-index: 13;
  min-width: 288px;
  max-width: 320px;
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
}
.sidebar-menu .header__text {
  max-width: 100%;
}
.sidebar-menu .header__menu-items {
  color: #fff;
}
.sidebar-menu._open-menu {
  transform: translateX(0);
}
@media (min-width: 768.98px) {
  .sidebar-menu {
    display: none;
  }
}
.sidebar-menu .header__text {
  color: #fff;
}
.sidebar-menu__body {
  height: 100%;
  background-color: #243444;
}
.sidebar-menu__content {
  height: 100%;
  max-width: 400px;
  transform-origin: 200% 100%;
  box-shadow: -2px 0 7px #2b2b2b;
  border: 1px solid rgba(128, 128, 128, 0.322);
  border-top: none;
  border-bottom: none;
}
.sidebar-menu__menu {
  display: flex;
  flex-direction: column;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.6);
  border-radius: 3px;
  background-clip: padding-box;
  transform: translate3d(0, 0, 0);
  position: relative;
  height: 100%;
  transform-origin: 0 50%;
  text-shadow: none;
}
.sidebar-menu__menu .header__menu-items {
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 4rem;
}
.sidebar-menu__menu-header {
  padding: 20px 10px;
  line-height: 1;
  border-bottom: 1px solid rgb(94, 77, 168);
  display: flex;
  justify-content: center;
  gap: 1.6rem;
